/* price chart */

.priceChart {
  position: relative;
  font-size: 120px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  background: #000000;
}
.priceChart span {
  width: 3.33em;
  line-height: 3.33em;
  font-size: 0.3em;
  position: absolute;
  width: 100%;
  z-index: 1;
  left: 0;
  top: 0;
  width: 5em;
  line-height: 5em;
  font-size: 0.2em;
  display: flex;
  justify-content: center;
  text-align: center;
  white-space: nowrap;
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease-out;
  color: rgba(255, 255, 255, 0.7);
  font-family: "sans-serif";
  font-style: normal;
  -webkit-user-select: none;
          user-select: none;
  -webkit-touch-callout: none;
}
.priceChart:after {
  position: absolute;
  top: 0.08em;
  left: 0.08em;
  display: block;
  content: " ";
  border-radius: 50%;
  background: linear-gradient(153.09deg, #000000 -14.22%, #363636 91.92%);
  /* background: linear-gradient(153.09deg, #000000 -14.22%, #363636 91.92%); */
  width: 0.84em;
  height: 0.84em;
  transition-property: all;
  transition-duration: 0.2s;
  transition-timing-function: ease-in;
}
.priceChart:hover:after {
  top: 0.04em;
  left: 0.04em;
  width: 0.92em;
  height: 0.92em;
}

.priceChart .bar {
  position: absolute;
  border: 0.08em solid;
  border-image: linear-gradient(153.09deg, #301ef8 -14.22%, #c412c8 91.92%);
  border-image-slice: 1;
  width: 117px;
  height: 119px;
  clip: rect(0em, 0.8em, 1em, 0em);
  border-radius: 50%;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  border: 0.08em solid transparent;
  background: linear-gradient(153.09deg, #301ef8 -14.22%, #c412c8 91.92%)
    border-box;
  /* -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0); */
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}
