.slider{
    position: relative;
    width: 200px;
    height: 200px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: rotate 40s linear infinite;
            animation: rotate 40s linear infinite;
    /* -webkit-box-reflect: below 1px linear-gradient(transparent, transparent, #000); */
}

@-webkit-keyframes rotate {
    0%{
        -webkit-transform: perspective(1000px) rotateY(0deg);
                transform: perspective(1000px) rotateY(0deg);
    }
    100%{
        -webkit-transform: perspective(1000px) rotateY(360deg);
                transform: perspective(1000px) rotateY(360deg);
    }
}

@keyframes rotate {
    0%{
        -webkit-transform: perspective(1000px) rotateY(0deg);
                transform: perspective(1000px) rotateY(0deg);
    }
    100%{
        -webkit-transform: perspective(1000px) rotateY(360deg);
                transform: perspective(1000px) rotateY(360deg);
    }
}
.box-1{
    --i: 1;
}
.box-2{
    --i: 2;
}
.box-3{
    --i: 3;
}
.box-4{
    --i: 4;
}
.box-5{
    --i: 5;
}
.box-6{
    --i: 6;
}
.box-7{
    --i: 7;
}
.box-8{
    --i: 8;
}
.slider div{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 280px;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    background: #261E71;
    -webkit-transform: rotateY(calc(var(--i)*70deg)) translateZ(270px);
            transform: rotateY(calc(var(--i)*70deg)) translateZ(270px);
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.356);
}

.slider div img{
    width: 90%;
    border-radius: 10px;
    object-fit: cover;
    margin-bottom: 25px;
    height: 45%;
    transition: 2s;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.356);

}
.slider div h1{
    width: 100%;
    text-align: center;
    color: #fff;
    margin-bottom: 10px;
    
}
.slider div a{
    color: #261E71;
    width:100px;
    height: 35px; 
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 5px;
    font-size: 0.9rem;
    letter-spacing: 1px;
    border-radius: 30px;
    background: #fff;
    margin-bottom: 20px;
    transition: 0.5s ease-in-out;
    box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.659);
}
.slider div a:hover{
    color: #fff;
    background-color: #261E71;
    box-shadow: 0px 5px 25px rgba(2, 133, 248, 0.659);
}

.slider div:hover img{
    -webkit-transform: translateY(-24px) scale(1.15);
            transform: translateY(-24px) scale(1.15);
}
    
